    display: grid;
    grid-template-rows: 1fr 10fr 1fr;
    padding: 1rem 0 0 0;
}

footer .footer-title {
    justify-self: center;
}

footer .contact-me-container {
    display: grid;
}

footer .contact-me {
    display: flex;
    align-items: center;
    padding: 0 1rem 1rem 1rem;
    margin: 0 1rem 1rem 1rem;
}

footer .contact-me img {
    width: 4rem;
}

footer .contact-me span {
    margin-left: 1.2rem;
}

