    margin-bottom: 0;
    padding: 0.5rem;
    font-weight: 700;
    border-radius: 50% 50% 0 0;
    border: 3px 0 0 0 dotted rgb(0, 0, 0);
    color: black;
    background-color: rgb(255, 255, 255);
    background-image: linear-gradient(to bottom,
            #ffffff, #d3d3d3, #a9a9a9, #808080, #5a5a5a);
