___CSS_LOADER_EXPORT___.push([module.id, `main {
    display: grid;
    grid-template-rows: 1fr 15fr 5fr;
    padding: 1rem;
    justify-items: center;
    background-color: grey;
}

main .main-header {
    margin: 1rem 0 7rem 0;
}

/* link icons */

main .main-header .link-icons-container {
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr 1fr;
    padding: 1.5rem;
}

main .link-icons-container a {
    width: 10rem;
    margin: 1rem;
    background-color: transparent;
    border: none;
    border-radius: 15%;
    text-decoration: none;
}

main .link-icons-container a:hover {
    background-color: rgba(5, 5, 5, 0.267);
}

main .link-icons-container a .new-tab-indicator {
    font-size: 2rem;
}

/* Web Apps */
main .apps-section {
    display: grid;
    grid-template-rows: 1fr 50fr;
    justify-items: center;
}

main .apps-section .apps-container {
    display: grid;
}

main .apps-container .web-app-card {
    display: grid;
    grid-template-rows: 2fr 3fr 5fr 3fr;
    justify-items: center;
    border: 1px solid rgb(0, 0, 0);
    margin: 1rem 0 7rem 0;
    padding: 1rem;
    border-radius: 16px;
    background-color: rgb(255, 255, 255);
}

main .web-app-card .app-title {
    padding: 0.2rem;
}

main .web-app-card .button-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

main .web-app-card .button-container button {
    width: 20rem;
    margin: 1rem;
    border-radius: 10%;
}

main .web-app-card .button-container button img {
    width: 10rem;
}

/* Positions */

main .positions-section {
    display: grid;
    justify-items: center;
    grid-template-rows: 1fr 25fr;
    margin: 3rem 0 1rem 0;
}

main .positions-section .positions-container {
    display: grid;
}

main .positions-container .position {
    display: grid;
    grid-template-rows: repeat(3, 2fr) 5fr;
    border: 1px solid rgb(0, 0, 0);
    background-color: rgb(115, 255, 90);
    padding: 1rem;
    margin: 1rem 0 7rem 0;
    border-radius: 8px;
}

main .position .position-title {
    text-align: center;
}

main .position .company-name {
    text-align: center;
    border-bottom: 1px dotted rgb(0, 0, 0);
}

main .position .position-dates {
    display: grid;
    grid-template-columns: 8fr 1fr 8fr;
    justify-items: center;
    justify-content: center;
    align-items: center;
}

main .position .position-dates .dash {
    margin: 0 1rem 0 1rem;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,gCAAgC;IAChC,aAAa;IACb,qBAAqB;IACrB,sBAAsB;AAC1B;;AAEA;IACI,qBAAqB;AACzB;;AAEA,eAAe;;AAEf;IACI,aAAa;IACb,qBAAqB;IACrB,8BAA8B;IAC9B,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,6BAA6B;IAC7B,YAAY;IACZ,kBAAkB;IAClB,qBAAqB;AACzB;;AAEA;IACI,sCAAsC;AAC1C;;AAEA;IACI,eAAe;AACnB;;AAEA,aAAa;AACb;IACI,aAAa;IACb,4BAA4B;IAC5B,qBAAqB;AACzB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,mCAAmC;IACnC,qBAAqB;IACrB,8BAA8B;IAC9B,qBAAqB;IACrB,aAAa;IACb,mBAAmB;IACnB,oCAAoC;AACxC;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,YAAY;AAChB;;AAEA,cAAc;;AAEd;IACI,aAAa;IACb,qBAAqB;IACrB,4BAA4B;IAC5B,qBAAqB;AACzB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sCAAsC;IACtC,8BAA8B;IAC9B,mCAAmC;IACnC,aAAa;IACb,qBAAqB;IACrB,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,sCAAsC;AAC1C;;AAEA;IACI,aAAa;IACb,kCAAkC;IAClC,qBAAqB;IACrB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,qBAAqB;AACzB","sourcesContent":["main {\n    display: grid;\n    grid-template-rows: 1fr 15fr 5fr;\n    padding: 1rem;\n    justify-items: center;\n    background-color: grey;\n}\n\nmain .main-header {\n    margin: 1rem 0 7rem 0;\n}\n\n/* link icons */\n\nmain .main-header .link-icons-container {\n    display: grid;\n    justify-items: center;\n    grid-template-columns: 1fr 1fr;\n    padding: 1.5rem;\n}\n\nmain .link-icons-container a {\n    width: 10rem;\n    margin: 1rem;\n    background-color: transparent;\n    border: none;\n    border-radius: 15%;\n    text-decoration: none;\n}\n\nmain .link-icons-container a:hover {\n    background-color: rgba(5, 5, 5, 0.267);\n}\n\nmain .link-icons-container a .new-tab-indicator {\n    font-size: 2rem;\n}\n\n/* Web Apps */\nmain .apps-section {\n    display: grid;\n    grid-template-rows: 1fr 50fr;\n    justify-items: center;\n}\n\nmain .apps-section .apps-container {\n    display: grid;\n}\n\nmain .apps-container .web-app-